  background-position: 9% 47%, 90% 65%, 85% 10%, 4% 66%, 0 10%, right 0, 0 0, 100% 0;
  background-repeat: no-repeat;
  background-size: auto, auto, auto, auto, auto, auto, cover, cover;
}

.autorization {
  height: 100%;
  padding-top: 140px;
  padding-bottom: 200px;
}

.autorization__inner {
  padding-top: 220px;
  padding-bottom: 60px;
}

.autorization__title {
  color: #fff;
  -webkit-text-fill-color: transparent;
  text-align: center;
  background: linear-gradient(280.98deg, #21d4fd 42.54%, #e40ecf 48.07%);
  -webkit-background-clip: text;
  width: 100%;
  margin-bottom: 56px;
  font-size: 49px;
  font-weight: 700;
  line-height: 78px;
  display: inline-block;
}

.autorization__form {
  background: linear-gradient(0deg, #143238 0%, #257b8e 100%);
  border: 1px solid #257b8e;
  border-radius: 11px;
  max-width: 660px;
  margin: 0 auto 25px;
  padding: 35px 40px 55px 35px;
}

.autorization__form-title {
  color: #fff;
  text-align: left;
  margin-bottom: 30px;
  font-size: 32px;
  font-weight: 700;
  line-height: 20px;
}

.autorization__form-label {
  color: #fff;
  text-align: left;
  flex-direction: column;
  margin-bottom: 20px;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  display: flex;
}

.autorization__form-input {
  cursor: grab;
  color: #fff;
  text-align: left;
  background-color: #0000;
  border: 1px solid #21d4fd;
  border-radius: 11px;
  margin-top: 5px;
  padding: 7px 28px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  transition: all .3s ease-in-out;
}

.autorization__form-input::placeholder {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
}

.autorization__form-input:hover {
  box-shadow: 10px -10px 45px 10px #21d4fd;
}

.autorization__btns-box {
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 11px;
  display: grid;
}

.autorization__btn-ok {
  color: #fff;
  text-align: center;
  background: linear-gradient(90deg, #e40ecf19 0%, #21d5fd2e);
  border-radius: 8px;
  min-height: 60px;
  padding: 17px 50px;
  font-size: 18px;
  font-weight: 700;
  line-height: 15.5px;
  position: relative;
}

.autorization__btn-ok:after {
  content: "Enter";
  text-align: center;
  color: #fff;
  background: linear-gradient(90deg, #e40ecf 0%, #21d4fd 100%);
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  font-weight: 700;
  line-height: 15.5px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  inset: 0;
  transform: translate(0);
}

.autorization__btn-ok:hover:after {
  transform: translate(-5%, -25%);
}

.autorization__btn-help {
  color: #0000;
  text-align: center;
  background: none;
  border: 1px solid #e40ecf30;
  border-radius: 8px;
  min-height: 60px;
  padding: 17px 50px;
  font-size: 18px;
  font-weight: 700;
  line-height: 15.5px;
  position: relative;
}

.autorization__btn-help:after {
  content: "Support";
  text-align: center;
  color: #fff;
  background: none;
  border: 1px solid #e40ecf;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 15.5px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  inset: 0;
  transform: translate(0);
}

.autorization__btn-help:hover:after {
  transform: translate(-5%, -25%);
}

.autorization__btn-remind {
  color: #0000;
  text-align: center;
  border: 1px solid #7887901d;
  border-radius: 8px;
  min-height: 60px;
  font-size: 14px;
  font-weight: 400;
  line-height: 15.5px;
  position: relative;
}

.autorization__btn-remind:after {
  content: "Remind me of my password";
  text-align: center;
  color: #fff;
  background: none;
  border: 1px solid #788790;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  padding-left: 35px;
  padding-right: 35px;
  font-size: 15px;
  font-weight: 700;
  line-height: 15.5px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  inset: 0;
  transform: translate(0);
}

.autorization__btn-remind:hover:after {
  transform: translate(-5%, -25%);
}

.autorization__separator {
  color: #fff;
  text-align: center;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 400;
  line-height: 26.01px;
}

.autorization__line {
  text-align: center;
  height: 10px;
}

.autorization__socials-title {
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
}

.autorization__socials-list {
  grid-template-columns: repeat(4, 55px);
  justify-content: center;
  align-items: center;
  gap: 27px;
  display: grid;
}

.autorization__socials-item {
  transition: all .3s ease-in-out;
  position: relative;
}

.autorization__socials-item:hover {
  transform: scale(1.2);
}

.autorization__socials-icon {
  position: absolute;
  top: 12px;
  left: 14px;
  transform: scale(1);
}

.autorization__socials-icon--google {
  left: 15px;
}

.autorization__socials-icon--telegram {
  left: 11px;
}

.autorization__socials-icon--facebook {
  left: 20px;
}

.autorization__socials-icon--linkedin {
  left: 15px;
}

@media (width <= 576px) {
  .autorization__title {
    text-align: center;
    margin-bottom: 40px;
    font-size: 49px;
    line-height: 44px;
  }

  .autorization__form {
    padding: 20px 17px;
  }

  .autorization__form-title {
    font-size: 24px;
    line-height: 55px;
  }

  .autorization__btn-ok, .autorization__btn-help {
