.navigation__mobile {
  &-background {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 50;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.7607843137);
    pointer-events: none;
    transition: all 0.3s ease-in-out;

    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }

  &-burger {
    display: none;
    background: 0 0;
    padding: 0;
    border: none;
    position: relative;
    width: 27px;
    height: 14px;
  }

  &-span {
    position: absolute;
    left: 0;
    width: 27px;
    height: 2px;
    background-color: #fff;

    &:first-child {
      top: 0;
    }

    &:nth-child(2n) {
      top: 45%;
    }

    &:nth-child(3n) {
      bottom: 0;
      left: 9;
      width: 18px;
    }
  }

  &-menu {
    padding: 25px 15px;
    z-index: 999;
    pointer-events: none;
    transform: translateX(-100%);
    opacity: 0;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    max-width: 350px;
    background: linear-gradient(0deg, #143137f5 0%, #257b8e 100%);
    transition: all 0.3s ease-in-out;

    &--active {
      transform: translateX(0%);
      pointer-events: all;
      opacity: 1;
    }
  }

  &-wrapper {
    position: relative;
  }

  &-close {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    top: 0;

    svg {
      width: 100%;
      max-width: -webkit-fill-available;
      height: 100%;
      stroke: white;
      fill: transparent;
      border-radius: 100%;
      transition: all 0.3s ease-in-out;
    }

    &:focus {
      svg {
        stroke: #069fc1ec;
        -webkit-box-shadow: 10px -10px 45px 10px #0b4654;
        -moz-box-shadow: 10px -10px 45px 10px #0b4654;
        box-shadow: 10px -10px 45px 10px #0b4654;
      }
    }

    &:hover {
      svg {
        stroke: #20bee2;
        -webkit-box-shadow: 10px -10px 45px 10px #21d4fd;
        -moz-box-shadow: 10px -10px 45px 10px #21d4fd;
        box-shadow: 10px -10px 45px 10px #21d4fd;
      }
    }

    &:active {
      svg {
        stroke: #07a1c4;
        -webkit-box-shadow: 10px -10px 45px 10px #0086a4;
        -moz-box-shadow: 10px -10px 45px 10px #0086a4;
        box-shadow: 10px -10px 45px 10px #0086a4;
      }
    }
  }

  &-list {
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
  }

  &-item {
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &-link {
    color: #aeaeae;
    font-size: 30px;
    font-weight: 400;
    line-height: 22.18px;
    text-decoration: none;
    display: block;
    padding: 5px 0;
    position: relative;
    z-index: 1;
    text-align: left;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(to right, #c414b2, #c414b2 50%, #fff 50%);
    background-size: 200% 100%;
    background-position: -100%;
    transition: all 0.2s ease-in-out;

    &:before {
      display: block;
      content: "";
      width: 0;
      height: 3px;
      bottom: 5px;
      left: 0;
      bottom: -3px;
      z-index: 0;
      position: absolute;
      background: #c414b2;
      outline: none;
      transition: all 0.3s ease-in-out;
    }

    &:focus {
      font-size: 35px;
      outline: none;
      -webkit-text-fill-color: #c414b2;
      color: #c414b2;
    }

    &:hover {
      background-position: 0%;

      &:before {
        width: 60%;
      }
    }
  }

  &-person {
    margin-bottom: 50px;

    &__img {
      width: 60px;
      height: 60px;
    }
  }

  &-messengers {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 50%;

    &__item {
      a {
        outline: none;
        &:focus {
          svg {
            fill: #d80bc4;
          }
        }
        &:hover {
          svg {
            fill: #c414b2;
          }
        }
        &:active {
          svg {
            fill: #470240;
          }
        }
        svg {
          fill: #f5f5f5;
          width: 30px;
          height: 30px;
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .navigation__mobile {
    &-burger {
      display: block;
    }
  }
}
